/* 全体の背景など */
.app-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  font-family: "Helvetica Neue", Arial, sans-serif;
}

/* ボタンが並ぶエリア */
/* 路線ボタンの基本設定（色は JS 側で制御するので、共通設定のみ残す） */
.line-selector button {
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  border-radius: 50px;
  border: 2px solid; /* 色は style 属性で決まる */
  transition: all 0.2s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}

/* 選択されているボタンの追加エフェクト */
.line-selector button.active {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  transform: translateY(-3px);
}

/* 駅情報カードの見出しも、路線の色に合わせると綺麗です */
.station-card h2 {
  font-size: 1.8rem;
  margin-bottom: 10px;
  /* backendから来る line_color を使うために、ここも JS 側で制御してもいいですね */
}

/* 選択されているボタンのデザイン */
.line-selector button.active {
  background-color: #007bff;
  color: white;
  border-color: #007bff;
  box-shadow: 0 4px 8px rgba(0, 123, 255, 0.3);
  transform: translateY(-2px); /* 選択時に少し浮き上がる */
}

/* ボタンを押した瞬間の動き */
.line-selector button:active {
  transform: translateY(0);
}

/* 駅情報カードのデザイン */
.station-card {
  background: white;
  padding: 30px;
  border-radius: 20px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}
